.nav {
  width: 100%;
  display: flex;
  justify-content: center;
}

.nav-list {
  display: flex;
  column-gap: .1rem;
  font-size: .9rem;
  letter-spacing: .1rem;

  @media(min-width: 768px) {
    column-gap: 1rem;
    font-size: 1.3rem;
  }
}

.nav-list__link {
  position: relative;
  color: #324d67;
  padding: 0.5rem;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    height: 2px;

    background-color: #c5a491;
    transform: scaleX(0);
    transform-origin: center;

    transition: transform 0.4s ease;
  }
}

.nav-list__link {
  &:hover {
    &::before {
      transform: scaleX(1);
    }
  }
}

.active {
  font-weight: 600;
}