.card {
  position: relative;
  max-width: 200px;
  display: flex;
  border-radius: 4px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.25);
  height: 100%;

  &:hover {
    cursor: pointer;
  }

  &:hover .card__title {
    color: #f60;
  }
}

.card__overlay {
  @extend %overlay;
  position: absolute;
  display: block;
  opacity: 0;
  transition: opacity .3s;

  &:hover {
    opacity: .1;
  }
}

.rating {
  position: absolute;
  top: 20px;
  left: -5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 20px;
  color: white;
  font-weight: 600;
  border-radius: 5px;

  &--high {
    background-color: #3bb33b;
  }

  &--low {
    background-color: #777;
  }
}

.card__text-info {
  display: grid;
  padding: 10px;
  row-gap: 7px;
}

.card__img {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.card__title {
  font-family: "Arial", "sans-serif";
  font-size: 1rem;
  font-weight: 600;
  font-size: 1rem;
  color: #000;
  transition: color .2s;

}

.card__extra-info {
  font-size: .9rem;
  color: rgba(0, 0, 0, .5);
}