.form {
  margin: 0 20px;
  padding: 10px 20px;
  max-width: 1440px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 5px;
  border: 3px solid #eaded7;
  border-radius: 10px;
}

.validation-message {
  color: red;
}

.input-wrapper,
.select-wrapper {
  min-height: 5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 5px;
}

.label {
  font-size: 1rem;
  color: #324d67;
  font-weight: 600;

  @media(min-width: 768px) {
    font-size: 1.2rem;
  }
}

.input,
.select {
  padding: 4px 15px;
  border: 1px solid #324d67;
  box-sizing: border-box;
  border-radius: 10px;
  font-size: .9rem;
  cursor: pointer;
}

.input--checkbox {
  transform: translate(3px, 2px) scale(1.3);
}

.form__btn {
  padding: 5px;
  margin: 0 auto;
  margin-bottom: 5px;
  min-width: 200px;
  border-radius: 10px;
  background-color: #ab7a5f;
  border: none;
  cursor: pointer;
  color: white;
  font-size: 1rem;
  letter-spacing: .1rem;
  transition: .4s;

  &:hover {
    background-color: #c5a491;
    color: #453227;
  }

  &:disabled {
    background-color: dimgrey;
    color: linen;
    opacity: .6;
    cursor: auto;
  }

  @media(min-width: 768px) {
    padding: 10px;
    margin: 0;
    margin-bottom: 5px;
  }
}

.validation-message {
  font-size: 1rem;

  @media(min-width: 768px) {
    font-size: 1.1rem;
  }
}