.footer {
  background-color: #000;
}

.footer__wrapper {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  max-width: 1440px;
  padding: 1rem;
  color: #f1f5f8;
  font-size: 1rem;

  @media(min-width: 768px) {
    padding: 1rem 3rem;
    font-size: 1.1rem;
  }
}

.footer__author {
  color: #f1f5f8;
  transition: .3s;

  &:hover {
    color: #d1b6a8;
  }
}

.footer__copyright {
  color: #d1b6a8;
}

.footer__school-logo {
  width: 80px;
  transition: .3s;
  
  @media(min-width: 768px) {
    width: 110px;
  }

  &:hover {
    fill: #d1b6a8;
  }
}