%overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: .7;
  z-index: 2;
}

.wrapper {
  margin: 0 auto;
  padding: 20px 0;
  box-sizing: border-box;
  max-width: 1440px;
}

