.search-input {
  box-sizing: border-box;
  padding: 0.5rem;
  width: 70%;

  border-radius: .25rem;
  border: none;

  background: #f1f5f8;
  letter-spacing: .1rem;
  outline: none;
  transition: box-shadow .3s;
  &:hover {
    box-shadow: -1px -1px 3px 0 #829ab0, 1px 1px 3px 0 #829ab0;
  }

  &:focus {
    box-shadow: -1px -1px 3px 0 #829ab0, 1px 1px 3px 0 #829ab0;
  }

  @media (min-width: 768px) {
    width: 50%;
    font-size: 1rem;
    padding: .8rem;
  }
}

.home-page__search-input {
  margin: 0 auto;
  margin-top: 1rem;

  @media (min-width: 768px) {
    margin-top: 2rem;
  }
}