* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

html {
  height: 100%;
}

body {
  height: 100%;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-size: .875rem;
}

#root {
  height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
}