.forms-container {
  padding: 10px 0;
  display: grid;
  row-gap: 10px;
}

.forms__main-content-wrapper {
  display: grid;
  max-width: 1440px;
  width: 100%;

  @media(min-width: 768px) {
    grid-template-columns: repeat(2, 1fr);
    margin: 0 auto;
  }
}

.forms__cards-container {
  margin: 0 20px;
  padding: 10px 20px;
  box-sizing: border-box;

  min-height: 300px;
  border: 3px solid #eaded7;
  border-radius: 10px;
}

.forms-list {
  padding: 20px 10px;
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
  
}

.forms-list__item {
  display: flex;
}