.movie-modal {
  position: fixed;
  top: 50%;
  right: 50%;

  padding: .5rem;
  display: grid;
  row-gap: 5px;
  max-height: 80vh;
  width: 80%;
  z-index: 3;
  border-radius: 1rem;
  background-color: white;
  max-width: 800px;
  overflow-y: auto;

  pointer-events: none;
  opacity: 0;

  transform: scale(1.15) translate(45%, -45%);

  &--open {
    pointer-events: auto;
    opacity: 1;
    transform: scale(1) translate(50%, -50%);
    transition: transform 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946), opacity 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946);
  }

  @media(min-width: 400px) {
    padding: 20px;
  }

  @media(min-width: 768px) {
    grid-template-rows: auto auto min-content;
    grid-template-areas: 'img title' 'img list-title' 'img list';
    column-gap: 20px;
  }

  @media(min-width: 900px) {
    padding: 30px;
  }

}

.movie-modal__img {
  border-radius: 10px;
  width: 260px;
  margin: 0 auto;

  @media(min-width: 768px) {
    grid-area: img;
  }

  @media(min-width: 900px) {
    width: 300px;
  }
}

.movie-modal__title {
  font-size: 1.4rem;
  font-weight: 700;

  @media(min-width: 768px) {
    grid-area: title;
    font-size: 1.6rem;
  }
}

.movie-modal__list-title {

  align-self: center;

  @media(min-width: 768px) {
    grid-area: list-title;
    height: min-content;
    font-size: 1.3rem;
  }
}

.movie-modal-list {
  display: grid;
  row-gap: 5px;
  font-size: .9rem;


  @media(min-width: 768px) {
    grid-area: list;
    font-size: 1rem;
  }
}

.movie-modal-list__item {
  display: grid;
  grid-template-columns: 120px auto;
  color: #333;

  @media(min-width: 768px) {
    grid-template-columns: 150px auto;
  }
}

.movie-modal-list__term {
  width: 100px;
  color: rgba(0, 0, 0, .6);
}

.movie-modal__close-btn {
  position: absolute;
  top: 10px;
  right: 15px;
  width: 30px;
  height: 30px;
  border: none;
  background-color: transparent;
  border-radius: 50%;
  cursor: pointer;
  transition: transform .4s;

  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 2px;
    height: 100%;
    background-color: rgb(0, 0, 0);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
    border-radius: 100px;
    transition: background-color .3s;
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  &:hover {
    transform: rotate(180deg);

    &::before,
    &::after {
      background-color: rgb(255, 0, 0);
    }
  }
}