.wrapper-card-list {

  @media(min-width: 460px) {
    padding: 40px;
  }
}

.card-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  row-gap: 10px;

  @media(min-width: 460px) {
    justify-content: center;
    flex-direction: row;
    gap: 25px;
  }
}

.card-list__item {
  max-width: 290px;
}

.load {
  width: 200px;
}